.add-list {
  &__popup {
    width: 240px;
    height: 150px;
    background: #fff;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 18px;
    position: relative;
    top: -25px;
    left: 10px;

    &-close-btn {
      position: absolute;
      right: -8px;
      top: -8px;
      cursor: pointer;
    }
    &-colors {
      margin-top: 12px;
      display: flex;
      justify-content: space-between;

      .badge {
        width: 16px;
        height: 16px;
        cursor: pointer;
        border: 2px solid transparent;

        &.active {
          border: 2px solid #525252;
        }
      }
    }
    .button {
      width: 100%;
      margin-top: 15px;
    }
  }
}